}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  padding: 0 10px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.title {
  font-size: 35px;
  margin-bottom: 20px;
  margin: 20px;
}

